import { useFormik } from 'formik'
import  { useEffect } from 'react'
import { useDispatch } from 'react-redux';
import * as Yup from 'yup';
import { addUserActionApi  } from '../../../Redux/reducer/quanLyNguoiDungReducer';
import { DispatchType } from '../../../Redux/configStote';
type Props = {}

export default function ModalAddUser({ }: Props) {
  const dispatch: DispatchType = useDispatch()
  
  const addUserFrm = useFormik({
    initialValues: {
      taiKhoan: '',
      matKhau: '',
      hoTen: '',
      soDT: '',
      maLoaiNguoiDung: '',
      maNhom: 'GP01',
      email: ''
    },
    validationSchema: Yup.object().shape({
        taiKhoan: Yup.string()
            .min(2, 'Ti khon qu t k t')
            .max(16, 'Ti khon qu 16 k t')
            .required('Ti khon khng c  trng'),

        matKhau: Yup.string()
            .required('Ti khon khng c  trng')
            .matches(/^(?=.*?[A-Z])(?=.*?[a-z])(?=.*?[0-9])(?=.*?[#?!@$%^&*-]).{8,}$/, 'Mt khu phi t nht 8 k t gm ch, s, v k t c bit'),

        hoTen: Yup.string()
            .required('Tn khng c  trng')
            .matches(/^[a-zA-Z_" + "" + "\\s]+$/, 'Ch nhp k t ch'),

        email: Yup.string().email('Email khng hp l').required('Email khng c  trng'),

        soDT: Yup.string()
            .required('S in thoi khng c  trng')
            .matches(/([\+84|84|0]+(3|5|7|8|9|1[2|6|8|9]))+([0-9]{8})\b/, 'S in thoi cha ng nh ng'),
        maLoaiNguoiDung: Yup.string().required('Vui lng chn ngi dng !'),
    }),
    onSubmit: (values) => {
      const actionApi = addUserActionApi(values);
    dispatch(actionApi)
    }
  }
  )
  useEffect(() => {
    
  },[])
  return (
    <div className="modal fade" id="addUserModal" aria-labelledby="exampleModalLabel" tabIndex={-1} aria-hidden="true">
      <div className="modal-dialog">
        <div className="modal-content form-user">
          <header className="head-form ">
            <h3 >THNG TIN NGI DNG</h3>
          </header>
          <div className="modal-body">
            {/* form user */}
            <form action="#" onSubmit={addUserFrm.handleSubmit}>
              <div className="form-group">
                <div className="input-group">
                  <div className="input-group-prepend">
                    <span className="input-group-text">
                      <i className='fa-solid fa-user'></i>
                    </span>
                  </div>
                  <input name='taiKhoan' className='form-control input-sm' placeholder='Ti Khon' type='text' onChange={addUserFrm.handleChange} onBlur={addUserFrm.handleBlur} />
                </div>
                {addUserFrm.errors.taiKhoan && <div className="text-err text-danger text-left">{addUserFrm.errors.taiKhoan}</div>}
              </div>
              <div className="form-group">
                <div className="input-group">
                  <div className="input-group-prepend">
                    <span className="input-group-text">
                      <i className='fa-solid fa-address-book'></i>
                    </span>
                  </div>
                  <input name='hoTen' className='form-control input-sm' placeholder='H v tn' type='text' onChange={addUserFrm.handleChange} onBlur={addUserFrm.handleBlur} />
                </div>
                {addUserFrm.errors.hoTen && <div className="text-err text-danger text-left">{addUserFrm.errors.hoTen}</div>}
              </div>
              <div className="form-group">
                <div className="input-group">
                  <div className="input-group-prepend">
                    <span className="input-group-text">
                      <i className='fa-solid fa-envelope'></i>
                    </span>
                  </div>
                  <input name='email' className='form-control input-sm' placeholder='Email' type='email' onChange={addUserFrm.handleChange} onBlur={addUserFrm.handleBlur} />
                </div>
                {addUserFrm.errors.email && <div className="text-err text-danger text-left">{addUserFrm.errors.email}</div>}
              </div>
              <div className="form-group">
                <div className="input-group">
                  <div className="input-group-prepend">
                    <span className="input-group-text">
                      <i className='fa-solid fa-key'></i>
                    </span>
                  </div>
                  <input name='matKhau' className='form-control input-sm' placeholder='Mt khu' type='password' onChange={addUserFrm.handleChange} onBlur={addUserFrm.handleBlur} />
                </div>
                {addUserFrm.errors.matKhau && <div className="text-err text-danger text-left">{addUserFrm.errors.matKhau}</div>}
              </div>
              <div className="form-group">
                <div className="input-group">
                  <div className="input-group-prepend">
                    <span className="input-group-text">
                      <i className='fa-solid fa-phone'></i>
                    </span>
                  </div>
                  <input name='soDT' className='form-control input-sm' placeholder='S in thoi' type='text' onChange={addUserFrm.handleChange} onBlur={addUserFrm.handleBlur} />
                </div>
                {addUserFrm.errors.soDT && <div className="text-err text-danger text-left">{addUserFrm.errors.soDT}</div>}
              </div>
              <div className="form-group">
                <div className="input-group">
                  <div className="input-group-prepend">
                    <span className="input-group-text">
                      <i className='fa-solid fa-user'></i>
                    </span>
                  </div>
                  <select className='form-control' name="maLoaiNguoiDung" id="" onChange={addUserFrm.handleChange} onBlur={addUserFrm.handleBlur}>
                    <option value="">Loi ngi dng</option>
                    <option value="GV">Gio v</option>
                    <option value="HV">Hc vin</option>
                  </select>
                </div>
                {addUserFrm.errors.maLoaiNguoiDung && <div className="text-err text-danger text-left">{addUserFrm.errors.maLoaiNguoiDung}</div>}
              </div>
              <div className="modal-footer">
                <button type="button" className="btn btn-danger" data-bs-dismiss="modal">ng</button>
                <button type="submit" className="btn ">Thm ngi dng</button>
              </div>
            </form>
          </div>

        </div>
      </div>
    </div>
  )
}