import { getStoreJson } from '../../../util/config'
import { useDispatch } from 'react-redux';
import { DispatchType } from '../../../Redux/configStote';
import { useFormik } from 'formik';
import * as Yup from 'yup';
import { UserModel, updateUserInfoActionApi } from '../../../Redux/reducer/quanLyNguoiDungReducer';

type Props = {

}
const UserUpdateInfo = (props: Props) => {
  const infoUser = getStoreJson('credentials');
  const dispatch: DispatchType = useDispatch();
  const updateFrm = useFormik<UserModel>({
    initialValues: {
      taiKhoan: infoUser?.taiKhoan,
      matKhau: infoUser?.matKhau,
      hoTen: infoUser?.hoTen,
      soDT: infoUser?.soDT,
      maLoaiNguoiDung: infoUser?.maLoaiNguoiDung,
      maNhom: 'GP01',
      email: infoUser?.email
    }, 
    validationSchema: Yup.object().shape({
      taiKhoan: Yup.string()
          .min(2, 'Ti khon qu t k t')
          .max(16, 'Ti khon qu 16 k t')
          .required('Ti khon khng c  trng'),

      matKhau: Yup.string()
          .required('Nhp li mt khu')
          .matches(/^(?=.*?[A-Z])(?=.*?[a-z])(?=.*?[0-9])(?=.*?[#?!@$%^&*-]).{8,}$/, 'Mt khu phi t nht 8 k t gm ch, s, v k t c bit'),

      hoTen: Yup.string()
          .required('Tn khng c  trng')
          .matches(/^[a-zA-Z_" + "" + "\\s]+$/, 'Ch nhp k t ch'),

      email: Yup.string().email('Email khng hp l').required('Email khng c  trng'),

      soDT: Yup.string()
          .required('S in thoi khng c  trng')
          .matches(/([\+84|84|0]+(3|5|7|8|9|1[2|6|8|9]))+([0-9]{8})\b/, 'S in thoi cha ng nh ng'),
      maLoaiNguoiDung: Yup.string().required('Vui lng chn ngi dng !'),
  }),
    onSubmit: (values: UserModel) => {
      console.log('values', values)
      const actionApi = updateUserInfoActionApi(values);
      dispatch(actionApi);
    }
  })

  return (
    <div className="modal fade" id="userUpdateInfo" tabIndex={-1} aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div className="modal-dialog">
        <div className="modal-content">
          <div className="head-form">
            <h3 className="header-title">Thng Tin Ngi Dng : {infoUser?.taiKhoan} </h3>
          </div>
          <div className="modal-body">
            <form action="#" onSubmit={updateFrm.handleSubmit}>
              <div className="form-group">
                <div className="input-group">
                  <div className="input-group-prepend">
                    <div className="input-group-text">
                      <i className='fa-solid fa-user'></i>
                    </div>
                  </div>
                  <input name='taiKhoan' className='form-control input-sm' placeholder='Ti Khon' type='text' value={updateFrm.values.taiKhoan}  onBlur={updateFrm.handleBlur} onChange={updateFrm.handleChange}/>
                
                </div>
                {updateFrm.errors.taiKhoan && <div className="text-err text-danger text-left">{updateFrm.errors.taiKhoan}</div>}
              </div>
              <div className="form-group">
                <div className="input-group">
                  <div className="input-group-prepend">
                    <div className="input-group-text">
                      <i className='fa-solid fa-address-book'></i>
                    </div>
                  </div>
                  <input name='hoTen' className='form-control input-sm' placeholder='H v tn' type='text'value={updateFrm.values.hoTen} onBlur={updateFrm.handleBlur} onInput={updateFrm.handleChange} />
                </div>
                {updateFrm.errors.hoTen && <div className="text-err text-danger text-left">{updateFrm.errors.hoTen}</div>}
              </div>
              <div className="form-group">
                <div className="input-group">
                  <div className="input-group-prepend">
                    <div className="input-group-text">
                      <i className='fa-solid fa-envelope'></i>
                    </div>
                  </div>
                  <input name='email' className='form-control input-sm' placeholder='Email' type='email'value={updateFrm.values.email} onBlur={updateFrm.handleBlur} onInput={updateFrm.handleChange} />
                </div>
                {updateFrm.errors.email && <div className="text-err text-danger text-left">{updateFrm.errors.email}</div>}

              </div>
              <div className="form-group">
                <div className="input-group">
                  <div className="input-group-prepend">
                    <div className="input-group-text">
                      <i className='fa-solid fa-key'></i>
                    </div>
                  </div>
                  <input name='matKhau' className='form-control input-sm' placeholder='Mt khu' type='password'value={updateFrm.values.matKhau} onBlur={updateFrm.handleBlur} onInput={updateFrm.handleChange} />
                </div>
                {updateFrm.errors.matKhau && <div className="text-err text-danger text-left">{updateFrm.errors.matKhau}</div>}

              </div>
              <div className="form-group">
                <div className="input-group">
                  <div className="input-group-prepend">
                    <div className="input-group-text">
                      <i className='fa-solid fa-phone'></i>
                    </div>
                  </div>
                  <input name='soDT' className='form-control input-sm' placeholder='S in thoi' type='text'value={updateFrm.values.soDT} onBlur={updateFrm.handleBlur} onInput={updateFrm.handleChange} />
                </div>
                {updateFrm.errors.soDT && <div className="text-err text-danger text-left">{updateFrm.errors.soDT}</div>}
              </div>
              <div className="form-group">
                <div className="input-group">
                  <div className="input-group-prepend">
                    <div className="input-group-text">
                      <i className='fa-solid fa-user'></i>
                    </div>
                  </div>
                  <select className='form-control' name="maLoaiNguoiDung" id=""value={updateFrm.values.maLoaiNguoiDung} onBlur={updateFrm.handleBlur} onInput={updateFrm.handleChange}>
                    <option value="">Loi ngi dng</option>
                    <option value="GV">Gio v</option>
                    <option value="HV">Hc vin</option>
                  </select>
                </div>
                {updateFrm.errors.maLoaiNguoiDung && <div className="text-err text-danger text-left">{updateFrm.errors.maLoaiNguoiDung}</div>}
              </div>

              <div className="modal-footer">
                <button type="button" className="btn btn-danger" data-bs-dismiss='modal'>ng</button>
                <button type="submit" className="btn" data-bs-dismiss="modal">Cp nht</button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  )
}

export default UserUpdateInfo