import React, { useState } from 'react'
import { NavLink } from 'react-router-dom'
import ButtonMouseEvent from '../../Components/ButtonProps/ButtonMouseEvent'
import { useFormik } from 'formik'
import { loginAsyncActionApi, signUpAsyncActionApi } from '../../Redux/reducer/quanLyNguoiDungReducer'
import { DispatchType } from '../../Redux/configStote'
import { useDispatch } from 'react-redux'
import * as Yup from 'yup'
type Props = {}

export interface UserLoginFrm {
    taiKhoan: string,
    matKhau: string,
}

export interface UserSignUpFrm {
    taiKhoan: string,
    matKhau: string,
    hoTen: string,
    email: string,
    soDT: string,
    maNhom: string,
}

const LoginMobile = (props: Props) => {
    const [classState, toggleClass] = useState(false);
    const dispatch: DispatchType = useDispatch();
    //formSingup
    const signUpFrm = useFormik<UserSignUpFrm>({
        initialValues: {
            taiKhoan: "",
            matKhau: "",
            hoTen: "",
            email: "",
            soDT: "",
            maNhom: "GP1",
        },
        validationSchema: Yup.object().shape({
            taiKhoan: Yup.string()
                .min(3, 'Ti khon qu t k t')
                .max(16, 'Ti khon qu 16 k t')
                .required('Ti khon khng c  trng'),

            matKhau: Yup.string()
                .required('Ti khon khng c  trng')
                .matches(/^(?=.*?[A-Z])(?=.*?[a-z])(?=.*?[0-9])(?=.*?[#?!@$%^&*-]).{8,}$/, 'Mt khu phi t nht 8 t gm ch, s, v k t c bit'),

            hoTen: Yup.string()
                .required('Tn khng c  trng')
                .matches(/^[a-zA-Z_" + "" + "\\s]+$/, 'Ch nhp k t ch'),

            email: Yup.string().email('Email khng hp l').required('Email khng c  trng'),

            soDT: Yup.string()
                .required('S in thoi khng c  trng')
                .matches(/([\+84|84|0]+(3|5|7|8|9|1[2|6|8|9]))+([0-9]{8})\b/, 'S in thoi cha ng nh ng'),
        }),
        onSubmit: (values: UserSignUpFrm) => {
            const actionApi = signUpAsyncActionApi(values)
            dispatch(actionApi);
        }
    })

    const loginFrm = useFormik<UserLoginFrm>({
        initialValues: {
            taiKhoan: '',
            matKhau: '',
        }, onSubmit: (values: UserLoginFrm) => {
            const actionApi = loginAsyncActionApi(values)
            dispatch(actionApi);
        }
    })

    return (
        <section className='login-mobile body-login'>
            <div className={`container login-form ${classState ? 'sign-up-mode' : ''}`} >
                <div className="form-container ">
                    <div className="signin-signup">
                        <div className="go-signin content">
                            <h3>Ti Khon Mi</h3>
                            <p>Lorem ipsum dolor consectetur adipisicing elit.</p>
                            <ButtonMouseEvent value={`ng K`} className={`btn transparent`} handleClick={() => {
                                toggleClass(true);
                            }} />

                        </div>
                        <form onSubmit={loginFrm.handleSubmit} action="#" className={`sign-in-form ${!classState ? 'animate__animated animate__fadeInRight animate__delay-3s' : ''}`}>
                            <h2 className='login-title'>ng Nhp</h2>

                            <div className="input-field">
                                <i className="fa-solid fa-user"></i>
                                <input type="text" placeholder="Ti khon" name="taiKhoan" onInput={loginFrm.handleChange} value={loginFrm.values.taiKhoan} />
                            </div>
                            <div className="input-field">
                                <i className="fa-solid fa-lock"></i>
                                <input type="password" placeholder="Mt khu" name='matKhau' onInput={loginFrm.handleChange} value={loginFrm.values.matKhau} />

                            </div>
                            <input type="submit" className='btn btn--common btn-primary' value='ng nhp' />

                            <p className='social-text'>Hoc ng nhp bng </p>

                            <div className="social-media">
                                <NavLink to="/" className="social_list fb"><i className="fab fa-facebook-f" /></NavLink>
                                <NavLink to="/" className="social_list gg"><i className="fab fa-google-plus-g" /></NavLink>
                                <NavLink to="/" className="social_list in"><i className="fab fa-linkedin-in" /></NavLink>
                            </div>
                        </form>
                    </div>

                    <div className="signin-signup">
                        <div className="go-signup content">
                            <h3> C Ti Khon</h3>
                            <p>Lorem ipsum dolor consectetur adipisicing elit.</p>
                            <ButtonMouseEvent value={`ng Nhp`} className={`btn transparent`} handleClick={() => {
                                toggleClass(false);
                            }} />

                        </div>
                        <form onSubmit={signUpFrm.handleSubmit} action="#" className={`sign-up-form`}>
                            <h2 className='login-titl'>ng K</h2>
                            <div className="input-field">
                                <i className="fa-solid fa-user"></i>
                                <input type="text" placeholder="Ti khon" name="taiKhoan" onInput={signUpFrm.handleChange} />
                                {signUpFrm.errors.taiKhoan ? <div className='message-err text-danger'>{signUpFrm.errors.taiKhoan}</div> : <div className='message'></div>}
                            </div>

                            <div className="input-field">
                                <i className="fa-solid fa-file-signature"></i>
                                <input type="text" placeholder="H Tn" name='hoTen' onInput={signUpFrm.handleChange} />
                                {signUpFrm.errors.hoTen ? <div className='message-err text-danger'>{signUpFrm.errors.hoTen}</div> : <div className='message'></div>}
                            </div>
                            <div className="input-field">
                                <i className="fa-solid fa-envelope"></i>
                                <input type="email" placeholder="Email" name="email" onInput={signUpFrm.handleChange} />
                                {signUpFrm.errors.email ? <div className='message-err text-danger'>{signUpFrm.errors.email}</div> : <div className='message'></div>}
                            </div>
                            <div className="input-field">
                                <i className="fa-solid fa-lock"></i>
                                <input type="password" placeholder="Mt khu" name='matKhau' onInput={signUpFrm.handleChange} />
                                {signUpFrm.errors.matKhau ? <div className='message-err text-danger'>{signUpFrm.errors.matKhau}</div> : <div className='message'></div>}
                            </div>

                            <div className="input-field">
                                <i className="fa-solid fa-phone-flip"></i>
                                <input type="text" placeholder="S in thoi" name='soDT' onInput={signUpFrm.handleChange} />
                                {signUpFrm.errors.soDT ? <div className='message-err text-danger'>{signUpFrm.errors.soDT}</div> : <div className='message'></div>}
                            </div>
                            <select id="" className='input-field'
                                onChange={signUpFrm.handleChange}
                                name='maNhom'
                            >
                                <option value="">Chn nhm ngi dng</option>
                                <option value="GP01">GP01</option>
                                <option value="GP02">GP02</option>
                                <option value="GP03">GP03</option>
                                <option value="GP04">GP04</option>
                                <option value="GP05">GP05</option>
                                <option value="GP06">GP06</option>
                                <option value="GP07">GP07</option>
                                <option value="GP08">GP08</option>
                                <option value="GP09">GP09</option>
                                <option value="GP010">GP010</option>
                            </select>

                            <input type="submit" className='btn btn--common btn-primary' value='ng k' />
                            <p className='social-text'>Hoc ng k bng </p>

                            <div className="social-media">
                                <NavLink to="/" className="social_list fb "><i className="fab fa-facebook-f" /></NavLink>
                                <NavLink to="/" className="social_list gg"><i className="fab fa-google-plus-g" /></NavLink>
                                <NavLink to="/" className="social_list in"><i className="fab fa-linkedin-in" /></NavLink>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </section>
    )
}
export default LoginMobile