import React, { useEffect } from 'react'
import { DispatchType } from '../../../Redux/configStote'
import { useDispatch } from 'react-redux'
import { useFormik } from 'formik';
import { UserModel, getUserArrActionApi } from '../../../Redux/reducer/quanLyNguoiDungReducer';
import * as Yup from 'yup';
import swal from 'sweetalert';
import { http } from '../../../util/config';

export interface Props {
  userUpdate: string | any,
}
export default function EditUserInfoModal(props: Props): JSX.Element {
  const dispatch: DispatchType = useDispatch()
  const updateUser = async (values: UserModel )=> {
    try {
      let result = await http.put('/api/QuanLyNguoiDung/CapNhatThongTinNguoiDung', values)
      console.log('result.data', result.data)
      if (result.request.status === 200) {
        swal({
          title: "Cp nht thnh cng",
          icon: "success",
          timer: 2000,
        });
        dispatch(getUserArrActionApi)
      }
    } catch (err) {
      console.log('err', err)
      swal({
        //  title: errors.response?.data,
        icon: "warning",
        text: ' xy ra li vui lng quay li trang ch hoc th li',
        timer: 2000,
      });
    }
  }
  const updateFrm = useFormik({
    initialValues: {
      taiKhoan: '',
      matKhau: "",
      hoTen: "",
      soDT: "",
      maLoaiNguoiDung: '',
      maNhom: "GP01",
      email: "",
    },
    validationSchema: Yup.object().shape({
      matKhau: Yup.string()
        .required('Nhp li mt khu')
        .matches(/^(?=.*?[A-Z])(?=.*?[a-z])(?=.*?[0-9])(?=.*?[#?!@$%^&*-]).{8,}$/, 'Mt khu phi t nht 8 k t gm ch, s, v k t c bit'),

      hoTen: Yup.string()
        .required('Tn khng c  trng')
        .matches(/^[a-zA-Z_" + "" + "\\s]+$/, 'Ch nhp k t ch'),

      email: Yup.string().email('Email khng hp l').required('Email khng c  trng'),

      soDT: Yup.string()
        .required('S in thoi khng c  trng')
        .matches(/([\+84|84|0]+(3|5|7|8|9|1[2|6|8|9]))+([0-9]{8})\b/, 'S in thoi cha ng nh ng'),
      maLoaiNguoiDung: Yup.string().required('Vui lng chn ngi dng !'),
    }),
    onSubmit: (values: UserModel) => {
      updateUser(values);
    }
  })

  useEffect(() => {
  }, [props.userUpdate])
  return (
    <div className="modal fade" id="editUserInfo">
      <div className="modal-dialog">
        <div className="modal-content">
          <header className="head-form ">
            <h3 >THNG TIN NGI DNG</h3>
          </header>
          <div className="modal-body">
            {/* form user */}
            <form action="#" onSubmit={updateFrm.handleSubmit}>
              <div className="form-group">
                <div className="input-group">
                  <div className="input-group-prepend">
                    <span className="input-group-text">
                      <i className='fa-solid fa-user'></i>
                    </span>
                  </div>
                  <input name='taiKhoan' className='form-control input-sm' placeholder='Ti Khon' type='text' onBlur={updateFrm.handleBlur} onInput={updateFrm.handleChange} disabled value={updateFrm.values.taiKhoan = props.userUpdate?.taiKhoan} />
                </div>
                {updateFrm.errors.taiKhoan && <div className="text-err text-danger text-left">{updateFrm.errors.taiKhoan}</div>}
              </div>
              <div className="form-group">
                <div className="input-group">
                  <div className="input-group-prepend">
                    <span className="input-group-text">
                      <i className='fa-solid fa-address-book'></i>
                    </span>
                  </div>
                  <input name='hoTen' className='form-control input-sm' placeholder='H v tn' type='text' onBlur={updateFrm.handleBlur} onInput={updateFrm.handleChange} value={updateFrm.values.hoTen}/>
                </div>
                {updateFrm.errors.hoTen && <div className="text-err text-danger text-left">{updateFrm.errors.hoTen}</div>}
              </div>
              <div className="form-group">
                <div className="input-group">
                  <div className="input-group-prepend">
                    <span className="input-group-text">
                      <i className='fa-solid fa-envelope'></i>
                    </span>
                  </div>
                  <input name='email' className='form-control input-sm' placeholder='Email' type='email' onBlur={updateFrm.handleBlur} onInput={updateFrm.handleChange} value={updateFrm.values.email }/>
                </div>
                {updateFrm.errors.email && <div className="text-err text-danger text-left">{updateFrm.errors.email}</div>}
              </div>
              <div className="form-group">
                <div className="input-group">
                  <div className="input-group-prepend">
                    <span className="input-group-text">
                      <i className='fa-solid fa-key'></i>
                    </span>
                  </div>
                  <input name='matKhau' className='form-control input-sm' placeholder='Mt khu' type='password' onBlur={updateFrm.handleBlur} onInput={updateFrm.handleChange} value={updateFrm.values.matKhau} />
                </div>
                {updateFrm.errors.matKhau && <div className="text-err text-danger text-left">{updateFrm.errors.matKhau}</div>}
              </div>
              <div className="form-group">
                <div className="input-group">
                  <div className="input-group-prepend">
                    <span className="input-group-text">
                      <i className='fa-solid fa-phone'></i>
                    </span>
                  </div>
                  <input name='soDT' className='form-control input-sm' placeholder='S in thoi' type='text'  onBlur={updateFrm.handleBlur} onInput={updateFrm.handleChange} value={updateFrm.values.soDT}/>
                </div>
                {updateFrm.errors.soDT && <div className="text-err text-danger text-left">{updateFrm.errors.soDT}</div>}
              </div>
              <div className="form-group">
                <div className="input-group">
                  <div className="input-group-prepend">
                    <span className="input-group-text">
                      <i className='fa-solid fa-user'></i>
                    </span>
                  </div>
                  <select className='form-control' name="maLoaiNguoiDung" id="" onBlur={updateFrm.handleBlur} onInput={updateFrm.handleChange} value={updateFrm.values.maLoaiNguoiDung }>
                    <option value="">Loi ngi dng</option>
                    <option value="GV">Gio v</option>
                    <option value="HV">Hc vin</option>
                  </select>
                </div>
                {updateFrm.errors.maLoaiNguoiDung && <div className="text-err text-danger text-left">{updateFrm.errors.maLoaiNguoiDung}</div>}
              </div>

              <div className="modal-footer">
                <button type="button" className="btn btn-danger" data-bs-dismiss="modal">ng</button>
                <button type="submit" className="btn ">Cp nht</button>
              </div>
            </form>
          </div>

        </div>
      </div>
    </div>
  )
}